import React, { Component } from 'react';
import {
  UserOutlined,
  GlobalOutlined,
  TrophyOutlined,
  CalendarOutlined,
  ClockCircleOutlined
} from '@ant-design/icons';
import { Tag } from 'antd';
import './index.less';

export default class SearchTips extends Component {
  constructor(props) {
    super(props)
    this.player = ['陈梦', '孙颖莎', '王曼昱', '王艺迪']
    this.nation = ['中国','日本','德国','韩国']
    this.competition = ['奥运会','亚锦赛','公开赛','世乒赛']
    this.match = ['女单四分之一决赛','女单半决赛','女单决赛']
    this.schedule = ['2021-07','2021-08','2022-07']
  }


  render() {
    const { search } = this.props;

    return (
      <div className='tips-wrapper' >
        <span className='header'>搜索示例</span>
        <div className='divider'></div>

        <div className='container'>
          <div className='tips-row'>
            <UserOutlined className='icon' />
            {
                this.player.map((item, index) => {
                  return <Tag className='tag' key={index} onClick={() => { search(item) }}>{item}</Tag>
                })
            }
          </div>
          <div className='tips-row'>
            <GlobalOutlined className='icon' />
            {
              this.nation.map((item, index) => {
                return <Tag className='tag' key={index} onClick={() => { search(item) }}>{item}</Tag>
              })
            }
          </div>
          <div className='tips-row'>
            <TrophyOutlined className='icon' />
            {
              this.competition.map((item, index) => {
                return <Tag className='tag' key={index} onClick={() => { search(item) }}>{item}</Tag>
              })
            }
          </div>
          <div className='tips-row'>
            <CalendarOutlined className='icon' />
            {
              this.match.map((item, index) => {
                let className = 'tag '+'match'+index
                return <Tag className={className} key={index} onClick={() => { search(item) }}>{item}</Tag>
              })
            }
          </div>
          <div className='tips-row'>
            <ClockCircleOutlined className='icon' />
            {
              this.schedule.map((item, index) => {
                return <Tag className='tag schedule' key={index} onClick={() => { search(item) }}>{item}</Tag>
              })
            }
          </div>
        </div>
      </div >
    )
  }
}