import React, { Component } from 'react'
import { Layout, Empty, Spin, Input, Pagination, Divider } from "antd";
import SearchTips from '../../../components/SearchTips';
import * as api from '../../../axios/api'
import './SearchView.less'

function isMobile() {
  let flag = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
  return flag;
}

/***展示****/
export default class SearchView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageList: [],
      hasMore: true,
      isShowLoading: true, 
      page: 1,
      searchWord:'',
      defaultInputValue: '',
      totalCount:0, 
      totalPage:0,
      isChangePage:false,
      showSearchTips: false
    }
    this.per_page = 20
  }

  componentWillMount() {
    this.queryPubilshedPages()
  }

  queryPubilshedPages= () => {
    let params = {
        per_page: this.per_page,
        page: 1,
    }
    api.getReport(params).then((response) => {
      const data = response.data.data
      this.setState({
        page: 1,
        totalCount: data.pageInfo.total,
        totalPage: data.pageInfo.pages,
        isShowLoading: false,
        pageList: data.reportList
      })
    })
  }

  pageNumberOnChange = (page) => {
    if (!this.state.searchWord || this.state.searchWord === '') {
      let params = {
        per_page: this.per_page,
        page: page,
      }
      this.setState({
        page: page,
        isShowLoading: true,
        isChangePage: true
      })
      api.getReport(params).then((response) => {
        const data = response.data.data
        if (data.reportList.length === 0) {
          this.setState({
            isShowLoading: false
          })
        } else {
          this.setState({
            isShowLoading: false,
            isChangePage: false,
            pageList: data.reportList
          })
        }
      })
    } else {
      this.search(page,true)
    }
  }

  setSelfState = (isShowLoading, pageList) => {
    if (pageList) {
      this.setState({
        isShowLoading: isShowLoading,
        pageList: pageList
      })
    } else {
      this.setState({
        isShowLoading: isShowLoading
      })
    }
  }

  onChange = (e) => {
    this.setState({
      searchWord: e.target.value
    })
  }

  onFocus = (e) => {
    // if (isMobile()) {
    //   this.setState({
    //     showSearchTips: true
    //   })
    // }
    this.setState({
      showSearchTips: true
    })
  }

  onPressEnter = (e) => {
    this.setState({
      searchWord: e.target.value
    })
    this.search()
  }

  search = (page = 1, isChangePage = false, words) => {
    let params = {
      per_page: this.per_page,
      page: page,
      words: words? words : this.state.searchWord
    }
    this.setState({
      page: page,
      showSearchTips: false,
      isShowLoading: true,
      isChangePage: isChangePage
    })
    api.search(params).then((response) => {
      const data = response.data.data
      this.setState({
        totalCount: data.pageInfo.total,
        totalPage: data.pageInfo.pages,
        pageList: data.reportList,
        isShowLoading: false,
        isChangePage: false,
      })
    })
  }

  setSearch = (word) => {
    this.setState({
      searchWord: word
    })
    this.search(1, false, word)
    this.props.closeSearch()
  }

  cancel = () => {
    this.props.closeSearch()
    this.setState({
      showSearchTips: false
    })
  }

  render() {

      return (
        <div className='list-container'>
          <div className='input-container'>
            <Input
              allowClear={true}
              className='searchDiv short'
              value={this.state.searchWord}
              onChange={(e) => this.onChange(e)}
              onFocus={(e) => this.onFocus(e)}
              onPressEnter={(e) => this.onPressEnter(e)}
              placeholder={'请输入要查询的数据快报信息'}
              suffix={
                <div onClick={() => this.search()} className='searchImage' />
              }
            />
            <span className='cancel-visible' onClick={()=>{this.cancel()}}>取消</span>
          </div>
          <SearchTips search={this.setSearch} />
        </div>)
    }
}



